// Footer

.footer {
  background-color: $darkest-grey;
  color: $grey;
  font-size: 13px;
  line-height: 18px;
}

.newsletter-email { width: 160px; }

@include breakpoint(m) {
  .newsletter-email,
  .newsletter-button {
    width: 100%;
    margin-top: 2px;
  }
}
@include breakpoint(l) {
  .newsletter-email { width: 160px; }
  .newsletter-button { width: auto; }
}