// Timetable
.timetable-row {
  border-top: 1px solid $light-grey;
  padding-bottom: 15px;
  padding-top: 15px;
  p {
    margin: 0;
  }
}
.timetable-time {
  @include breakpoint(m) {
    width: 200px;
    // float: left;
  }
}
.timetable-activity {
  overflow: hidden;
  padding-top: 4px;
}
.time {
  background-color: $lighter-grey;
  padding: 4px;
  border-radius: 4px;
  text-align: center;
  display: inline-block;
  font-size: 12px;
  line-height: normal;
  @include breakpoint(m) {
    width: 120px;
  }
}