// Location
.location {
  color: $white;
  position: relative;
  background-color: $light-grey;
}
.location-content {
  @include background-rgba($black, .5);
  position: absolute;
  height: 100%;
  width: 100%;
  z-index: 100;
  @include breakpoint(m) {
    width: 50%;
  }
}
.location-content-inner {
  padding: 60px 20px;
  @include breakpoint(m) {
    width: 300px;
    margin: 0 auto;
  }
}
.map {
  width: 100%;
  overflow: hidden;
  position: relative;
}
.google-map {
  height: 400px;
  width: 100%;
}