// Tunnel Zoom Animation
@keyframes tunnelZoom {
  0% { transform: scale(0); }
  100% { transform: scale(2); }
}
.tunnel-zoom { animation-name: tunnelZoom; }

.banner-section {
  background-color: #999;
  position: relative;
  overflow: hidden;
  &:before, &:after {
    content: "";
    position: absolute;
    top: 50%;
    left: 50%;
    margin-top: -500px;
    margin-left: -500px;
    width: 1000px;
    height: 1000px;
    border-radius: 100%;
    display: block;
  }
  &:before {
    background-color: #fff;
    animation-delay: 1s;
    @include animate(1s);
    @extend .tunnel-zoom;
  }
  &:after {
    background-color: #000;
    animation-delay: 1.2s;
    @include animate(1s);
    @extend .tunnel-zoom;
  }
}
.banner {
  background: url(../img/banner.jpg) 0 50% no-repeat;
  background-size: cover;
  color: $white;
  z-index: 1000;
  position: relative;
  animation-delay: 1.2s;
  @include animate(1s);
  @extend .fade-in;
  &:after {
    content: "";
    @include background-rgba($black, .6);
    position: absolute;
    display: block;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    animation-delay: 2s;
    @include animate(1s);
    @extend .fade-in;
  }

  .animation-1 {
    animation-delay: 3s;
    @include animate(1s);
    @extend .fade-in;
  }
  .animation-2 {
    animation-delay: 3.5s;
    @include animate(1s);
    @extend .fade-in;
  }
  .animation-3 {
    animation-delay: 4s;
    @include animate(1s);
    @extend .fade-in;
  }
  .animation-4 {
    animation-delay: 4.5s;
    @include animate(1s);
    @extend .fade-in;
  }
  .animation-5 {
    animation-delay: 5s;
    @include animate(1s);
    @extend .fade-in;
  }
  .animation-6 {
    animation-delay: 5.5s;
    @include animate(1.2s);
    @extend .fade-in-up;
  }
  .animation-7 {
    animation-delay: 5.7s;
    @include animate(1s);
    @extend .fade-in-up;
  }
  .animation-8 {
    animation-delay: 6s;
    @include animate(1s);
    @extend .fade-in;
  }
}

.banner-inner {
  position: relative;
  z-index: 1000;
}

.logo {
  font-size: 16px;
  line-height: 20px;
  color: $white;
  position: relative;
  padding-bottom: 10px;
  &:after {
    content: "";
    display: block;
    width: 20px;
    height: 2px;
    background-color: $white;
    position: absolute;
    bottom: 0;
    left: 50%;
    margin-left: -10px;
  }
}

.banner-heading {
  text-transform: uppercase;
  font-size: 14px;
  line-height: 20px;
  letter-spacing: 1px;
  color: $white;
  margin: 0;
  @include breakpoint(m) {
    font-size: 16px;
    line-height: 22px;
  }
  @include breakpoint(l) {
    font-size: 20px;
    line-height: 28px;
  }
}
.banner-sub-heading {
  text-transform: uppercase;
  letter-spacing: 1px;
  font-size: 30px;
  line-height: 36px;
  color: $secondary-orange;
  margin: 5px 0 0 0;
  position: relative;
  @include breakpoint(m) {
    font-size: 42px;
    line-height: 48px;
  }
  @include breakpoint(l) {
    font-size: 48px;
    line-height: 56px;
  }
  &:after {
    content: "";
    width: 30px;
    height: 2px;
    background-color: $white;
    display: block;
    position: absolute;
    bottom: 0;
    left: 50%;
    margin-left: -15px;
  }
}