// Shared Components

body {
  color: $darker-grey;
}

.margin-0 { margin: 0; }
.border-0 { border: 0; }

.hr {
  border: 0;
  height: 1px;
  background: $white;
  margin: 0;
}

// Forms
.field,
textarea {
  line-height: normal;
  border: 2px solid $white;
  padding: 8px;
  width: 100%;
  transition: all .3s;
  &:focus {
    outline: 0;
    border-color: $secondary-orange;
  }
}
::placeholder {
  color: $mid-grey;
}

// Buttons
.button {
  font-family: $base-heading-font-family;
  font-size: 12px;
  line-height: normal;
  color: $white;
  background-color: transparent;
  border: 2px solid #fff;
  padding: 10px 20px;
  text-transform: uppercase;
  text-decoration: none;
  cursor: pointer;
  transition: all .3s;
  &:hover, &:focus {
    color: $black;
    background-color: $white;
    outline: 0;
  }  
}
.button-primary {
  font-size: 10px;
  line-height: 14px;
  background-color: $primary-blue;
  border-color: transparent;
  color: $white;
  text-transform: uppercase;
  &:hover, &:focus {
    color: $white;
    background-color: $secondary-blue;
  }
}

.list {
  list-style: none;
  padding: 20px 0;
  margin: 0;
  & > li {
    padding-left: 24px;
    padding-right: 24px;
    position: relative;
    margin-bottom: 10px;
    &:before {
      content: "";
      width: 12px;
      height: 12px;
      position: absolute;
      top: 4px;
      left: 0;
      display: block;
      background: url(../img/compass.svg) 0 0 no-repeat;
      background-size: cover;
    }
  }
}

.list-columns {
  li {
    @include breakpoint(m) {
      flex: 0 0 50%;
      max-width: 50%;
    }
  }
}

// Forms
fieldset { border: 0; }

.heading {
  color: $black;
  text-transform: uppercase;
  margin: 0 0 8px 0;
}
.heading-secondary { color: $white; }
.heading-underline {
  text-transform: uppercase;
  padding-bottom: 15px;
  position: relative;
  &:after {
    content: "";
    display: block;
    width: 20px;
    height: 2px;
    background-color: $secondary-orange;
    position: absolute;
    bottom: 0;
    left: 0;
  }
}
.sub-heading {
  font-size: 11px;
  line-height: 18px;
  color: $primary-orange;
  text-transform: uppercase;
  margin: 0 0 20px 0;
}

// Social Links
.social-links {
  list-style: none;
  padding: 0;
  margin: 0;
  & > li {
    display: inline-block;
  }
}
.social {
  width: 20px;
  height: 20px;
  display: inline-block;
  margin-right: 15px;
  cursor: pointer;
  &:hover {
    transition: all .3s;
  }
}
.social-facebook {
  background: url(../img/facebook.svg) 50% 50% no-repeat;
  &:hover {
    background: url(../img/facebook-hover.svg) 50% 50% no-repeat;
  }
}
.social-twitter {
  background: url(../img/twitter.svg) 50% 50% no-repeat;
  &:hover {
    background: url(../img/twitter-hover.svg) 50% 50% no-repeat;
  }
}
.social-flickr {
  background: url(../img/flickr.svg) 50% 50% no-repeat;
  &:hover {
    background: url(../img/flickr-hover.svg) 50% 50% no-repeat;
  }
}
.social-instagram {
  background: url(../img/instagram.svg) 50% 50% no-repeat;
  &:hover {
    background: url(../img/instagram-hover.svg) 50% 50% no-repeat;
  }
}