// Gallery
.gallery-item { padding: 0; }
.gallery-item-link {
  overflow: hidden;
  height: 180px;
  position: relative;
  cursor: pointer;
  display: block;
  & > img {
    display: block;
    position: relative;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
  &:before, &:after {
    content: "";
    position: absolute;
    top: 50%;
    left: 50%;
    display: block;
    transition: all .3s;
  }
  &:before {
    z-index: 100;
    margin-top: -500px;
    margin-left: -500px;
    width: 1000px;
    height: 1000px;
    border-radius: 100%;
  }
  &:after {
    opacity: 0;
    z-index: 200;
    width: 24px;
    height: 24px;
    background: url(../img/eye.svg) 50% 50% no-repeat;
    background-size: cover;
    margin-top: -12px;
    margin-left: -12px;
  }
  &:hover {
    &:before {
      @include background-rgba($black, .7);
      @include animate(1.2s);
      @extend .tunnel-zoom;
    }
    &:after {
      opacity: 1;
      transition-delay: .2s;
    }
  }
  @include breakpoint(m) {
    height: 220px;
  }
  @include breakpoint(l) {
    height: 260px;
  }
}