// Overview
.summary { border-bottom: 1px solid #eee; }

.summary-list {
  list-style-type: none;
  padding: 0;
  margin: 0;
  & > li {
    text-align: center;
    padding: 20px 10px;
    height: 110px;
    border-left: 1px solid #eee;
    &:first-child {
      border-bottom: 1px solid #eee;
      @include breakpoint(m) {
        border-left: 1px solid #eee;
        border-bottom: 0;
      }
    }
    &:nth-child(2) {
      border-right: 1px solid #eee;
      border-bottom: 1px solid #eee;
      @include breakpoint(m) {
        border-right: 0;
        border-bottom: 0;
      }
    }
    &:nth-child(4) {
      border-right: 1px solid #eee;
    }
  }
}

.summary-label {
  font-size: 8px;
  line-height: 16px;
  letter-spacing: 2px;
  text-transform: uppercase;
  color: #999;
  margin-bottom: 5px;
}
.summary-value {
  font-family: $base-heading-font-family;
  font-size: 18px;
  line-height: 26px;
  font-weight: 700;
  text-transform: uppercase;
  color: #000;
}